import {useState, useEffect} from "react";

import { useSelector } from 'react-redux';
import { IReduxState } from "../../../store/slices/state.interface";
import { calcUserCollateralDetails } from '../../../store/slices/user-slice';
import { useDispatch } from "react-redux";
import {Trade} from '../../../container/trade';
import {AppDispatch} from "../../../store/store";

import { useWeb3Context } from "../../../hooks/web3Context";

import {	
	Box,
	Button,
	Container,  
  Grid,  
	Typography,
} from "@mui/material";

function CollateralBoard() {
	const dispatch = useDispatch<AppDispatch>();
  const { address, provider, chainID, connected } = useWeb3Context();
	const {perpetualObject} = Trade.useContainer();

  const isUserCollateralCalculated = useSelector<IReduxState, boolean>(state => {
		return state.user.isUserCollateralCalculated;
	});	

  const isUserCollateralInitialized = useSelector<IReduxState, boolean>(state => {
		return state.user.isUserCollateralInitialized;
	});	

  const freeCollateral = useSelector<IReduxState, string>(state => {
		return state.user.freeCollateral;
	});	

  const accountValue = useSelector<IReduxState, string>(state => {
		return state.user.accountValue;
	});	

  const CalcUserCollateralDetails = function() {
    if(connected === true) {
      dispatch(calcUserCollateralDetails({ address: address, networkID: chainID, perpetualObject: perpetualObject }));
    }		
	}	 

	useEffect(() => {
		CalcUserCollateralDetails();
		const timer = setInterval(() => CalcUserCollateralDetails(), 60000);	
		return () => clearInterval(timer);	
	}, [connected]);

	return (		
		<>			
      <Typography sx={{color: '#fff', fontSize: '16px', background: 'linear-gradient(180deg,rgba(61,105,101,.15),rgba(143,222,224, 0.15))', 
        paddingY: '14px', width: '100%', borderRadius: '20px', fontFamily: 'Montserrat Medium'}}>
        ❄️ Perpetual Protocol ❄️
      </Typography>
      <Grid container spacing={4} sx={{marginTop: '12px', marginLeft: '-16px', '& > .MuiGrid-item': {p: 2}}}>
        <Grid item lg={6} md={6} sm={6} xs={12}>   
          <Box sx={{paddingY: '12px', borderRadius: '10px', backgroundColor: 'rgba(29, 29, 29, 1)'}}>       
            <Typography sx={{color: 'rgba(145, 153, 167, 1)', fontSize: '20px', fontFamily: 'Montserrat Medium'}}>Total Collateral</Typography> 
            {isUserCollateralCalculated? <Typography sx={{color: 'white', fontSize: '24px', fontFamily: 'Montserrat Bold'}}>${accountValue}</Typography> : 
              [isUserCollateralInitialized? <Typography sx={{color: 'white', fontSize: '24px', fontFamily: 'Montserrat Bold'}}>${accountValue}</Typography> : 
                <Typography sx={{color: 'white', fontSize: '24px', fontFamily: 'Montserrat Bold'}}>$0.0</Typography>              
              ] 
            }            
          </Box>
        </Grid>
        <Grid item lg={6} md={6} sm={6} xs={12}>   
          <Box sx={{paddingY: '12px', borderRadius: '10px', backgroundColor: 'rgba(29, 29, 29, 1)'}}>       
            <Typography sx={{color: 'rgba(145, 153, 167, 1)', fontSize: '20px', fontFamily: 'Montserrat Medium'}}>Free Collateral</Typography> 
            {isUserCollateralCalculated? <Typography sx={{color: 'white', fontSize: '24px', fontFamily: 'Montserrat Bold'}}>${freeCollateral}</Typography> : 
              [isUserCollateralInitialized? <Typography sx={{color: 'white', fontSize: '24px', fontFamily: 'Montserrat Bold'}}>${freeCollateral}</Typography> : 
                <Typography sx={{color: 'white', fontSize: '24px', fontFamily: 'Montserrat Bold'}}>$0.0</Typography>              
              ] 
            } 
          </Box> 
        </Grid>
      </Grid>
		</>		
	)
}

export default CollateralBoard;