import {useState, useCallback, useEffect} from "react";
import { useSelector } from 'react-redux';
import { IReduxState } from "../../../store/slices/state.interface";
import { calcMarketDetails } from '../../../store/slices/markets-slice';
import { useDispatch } from "react-redux";
import {Trade} from '../../../container/trade';
import {AMM} from "../../../container/amm";
import {AppDispatch} from "../../../store/store";

import {	
	Box,	    
	Typography,
} from "@mui/material";

import MarketSelector from "./Marketselector";

function PriceHeader() {
	const dispatch = useDispatch<AppDispatch>();
	const {perpetualObject} = Trade.useContainer();
	const {marketticker} = AMM.useContainer();
	const isMarketInfoCalculated = useSelector<IReduxState, boolean>(state => {
		return state.markets.isMarketInfoCalculated;
	});	

	const isMarketInfoInitialized = useSelector<IReduxState, boolean>(state => {
		return state.markets.isMarketInfoInitialized;
	});	

	const marketIndexPrice = useSelector<IReduxState, string>(state => {
		return state.markets.marketIndexPrice;
	});

	const CalcMarketDetails = function() {
		dispatch(calcMarketDetails({ perpetualObject: perpetualObject, tickerSymbol: marketticker }));
	}	 

	useEffect(() => {
		CalcMarketDetails();
		const timer = setInterval(() => CalcMarketDetails(), 300000);	
		return () => clearInterval(timer);	
	}, [marketticker]);

	return (
		<Box sx={{backgroundColor: 'rgba(9, 9, 10, 1)', py: 1, px: 1}}>
			<Box sx={{display: 'flex', justifyContent: 'flex-start', alignItems: 'center', 
				backgroundColor: 'rgba(29, 29, 29, 1)',
				flexDirection: {xs: 'column', sm: 'row'},
				borderRadius: '10px',
				py: '4px'							
			}}>
				<MarketSelector />
				<Box sx={{display: 'flex', justifyContent: 'flex-start',
					borderLeft: {sm: '1px solid rgba(145, 153, 167, 0.8)'},
					pl: {sm: 2, md: 3, xl: 4},
					py: 1
				}}>
					<Box sx={{pr: {xs: 2, md: 3, xl: 4}}}>
						<Typography sx={{fontSize: '0.75rem', color: 'rgba(145, 153, 167, 1)', fontFamily: 'Montserrat'}}>Index Price</Typography>
						{isMarketInfoCalculated? <Typography sx={{color: 'rgb(255, 255, 255)', fontFamily: 'Montserrat Medium'}}>{marketIndexPrice}</Typography> :
							[isMarketInfoInitialized? <Typography sx={{color: 'rgb(255, 255, 255)', fontFamily: 'Montserrat Medium'}}>{marketIndexPrice}</Typography> : 
							<Typography sx={{color: 'rgb(255, 255, 255)', fontFamily: 'Montserrat Medium'}}>0.0</Typography>]
						}					
					</Box>
					<Box sx={{pr: {xs: 2, md: 3, xl: 4}}}>
						<Typography sx={{fontSize: '0.75rem', color: 'rgba(145, 153, 167, 1)', fontFamily: 'Montserrat'}}>Volume (24h)</Typography>
						<Typography sx={{color: 'rgb(255, 255, 255)', fontFamily: 'Montserrat Medium'}}>5,798,410.3</Typography>
					</Box>
					<Box sx={{pr: {xs: 2, md: 3, xl: 4}}}>
						<Typography sx={{fontSize: '0.75rem', color: 'rgba(145, 153, 167, 1)', fontFamily: 'Montserrat'}}>Funding Rate (8h)</Typography>
						<Typography sx={{color: 'rgb(255, 255, 255)', fontFamily: 'Montserrat Medium'}}>0.0071%</Typography>
					</Box>
				</Box>
			</Box>
		</Box>
	)
}

export default PriceHeader;