import { createSlice,createAsyncThunk, createSelector } from "@reduxjs/toolkit";
import { JsonRpcProvider, StaticJsonRpcProvider } from "@ethersproject/providers";
import { Networks, DEFAULD_NETWORK } from "../../constants/blockchain";
import { setAll } from "../../helpers/set-all";
import { PerpetualProtocol } from "@perp/sdk-curie";
import Big from "big.js";
import { RootState } from "../store";

interface ICalcMarketDetails { 
	perpetualObject: PerpetualProtocol;	
	tickerSymbol: string;
}

export const calcMarketDetails = createAsyncThunk("market/calcMarketDetails", 
	async({ perpetualObject, tickerSymbol }: ICalcMarketDetails) => {
		await perpetualObject.init();
		const Marketmap = perpetualObject.markets.marketMap;	
		const _marketIndexPrice = await perpetualObject.contractReader.getIndexPrice(Marketmap[tickerSymbol].baseAddress);
		console.log("Update index price", _marketIndexPrice.toFixed(2));
		return {
			marketIndexPrice: _marketIndexPrice.toFixed(2)
		}
});

const initialState = {
	isMarketInfoCalculated: false,
	isMarketInfoInitialized: false,	
}

export interface IMarketsSlice {	
	isMarketInfoCalculated: boolean,
	isMarketInfoInitialized: boolean,		
	marketIndexPrice: string,	
}

const marketsSlice = createSlice({
	name: "markets",
	initialState,
	reducers: {			
	},
	extraReducers: builder => {
		builder			
			.addCase(calcMarketDetails.pending, state => {			
				state.isMarketInfoCalculated = false;
			})
			.addCase(calcMarketDetails.fulfilled, (state, action) => {
				setAll(state, action.payload);
				state.isMarketInfoCalculated = true;	
				state.isMarketInfoInitialized = true;
			})
			.addCase(calcMarketDetails.rejected, (state, { error }) => {
				state.isMarketInfoCalculated = false;	
				console.log(error);
			})			
	},    
});

const baseInfo = (state: RootState) => state.markets;
export const { } = marketsSlice.actions;

export default marketsSlice.reducer;

export const getAppState = createSelector(baseInfo, markets => markets);