import { useEffect, useState } from "react";
import {
	AppBar,
	Box,
	Button,
	Link,       
	Typography,
} from "@mui/material";

import { useNavigate } from "react-router-dom";
import { useWeb3Context } from "../hooks/web3Context";
import { DEFAULD_NETWORK } from "../constants/blockchain";
import WalletIcon from '@mui/icons-material/Wallet';

const menuLists = [
  {
    link: "/trade",
    text: "Trade",
  },
  {
    link: "/pools",
    text: "Pools",
  },  
];

function ConnectMenu() {
	const { connect, disconnect, connected, web3, address, provider, providerChainID, checkWrongNetwork } = useWeb3Context();    
	const [isConnected, setConnected] = useState(connected);   
	let navigate = useNavigate();     

	let buttonText = "Connect";
	let clickFunc: any = connect;   

	if (isConnected) {
		buttonText = "Disconnect";
		clickFunc = disconnect;
	}    

	if (isConnected && providerChainID !== DEFAULD_NETWORK) {
		buttonText = "Wrong network";
		// buttonStyle = { backgroundColor: "rgb(255, 67, 67)" };
		clickFunc = () => {
			checkWrongNetwork();
		};
	}

	const handleClickMenu = (link: string) => {    
    navigate(link);
  };

	useEffect(() => {
		setConnected(connected);
	}, [web3, connected]);    

	return (
		<AppBar
			position="static"
			sx={{
				backgroundColor: 'rgba(9, 9, 10, 1)',
				backgroundImage: "none",
				py: 2,
				px: 2,
				borderBottom: '1px solid rgba(255, 255, 255, 0.16)'
			}}
		>
			
			<Box
				flexGrow={1}          
				justifyContent="space-between"         
				alignItems="center" 
				sx={{ ml: { xs: "20px", md: "60px" }, display: "flex"                        
				}}
			>
				<Typography
					variant="h4"
					fontWeight="700"
					textAlign="center"					                   
				>
					<Link href="/" sx={{textDecoration: 'none', color: 'white'}}>
						Perp V2
					</Link>
				</Typography>
				<Box sx={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', flexGrow: '1'}}>
					<Box sx={{display: 'flex', justifyContent: 'space-between', '& .MuiTypography-root:hover': {color: '#00ec96'}}}>
						{menuLists.map((list, key) => (
							<Typography key={key} sx={{pl: 4, cursor: 'pointer', fontFamily: 'Montserrat Bold', color: 'rgba(145, 153, 167, 1)'}} 
								onClick={() => handleClickMenu(list.link)}>
								{list.text}
							</Typography>
						))}
					</Box>
					<Button
						variant="contained"
						sx={{
							cursor: 'pointer', backgroundColor: 'transparent', 
							color: 'rgba(101, 230, 255, 1)', fontSize: '16px',                            
							'&:hover': {backgroundColor: 'rgba(101, 239, 255, 0.12)'}
						}}
						onClick={clickFunc}
					>
						<WalletIcon sx={{color: 'rgba(101, 230, 255, 1)', pr: 1}}/>
						{buttonText}
					</Button>  
				</Box>              
			</Box>            
		</AppBar>
	);
}

export default ConnectMenu;
